export default{
	name:'ks-page-scroller',
	props:{
		page:Number,
		pageCount:Number,
		height:{
			type:String,
			default:'100%'
		},
		refreshStatus:Boolean
	},
	data(){
		return {
			y:-49,
			refreshing:false,
			touchFlag:true,
			baseUrl:this.$store.state.SRC+"/config/app",
		}
	},
	watch:{
		refreshStatus(val){
			if(!val){
				this.y = -49
				this.refreshing = false
			}
		}
	},
	methods:{
		/**
		 * 手指触摸事件开始
		 */
		onTouchStart(e){
			if(this.refreshing){
				this.touchFlag = false
				return
			}
			this.touchFlag = true
			this.startPageY = e.changedTouches[0].pageY
		},
		/**
		 * 拖动中
		 */
		onTouchMove(e){
			if(!this.touchFlag){
				return
			}
			let pageY = e.changedTouches[0].pageY
			let y = -49 + (pageY - this.startPageY)
			y = Math.min(y,0)
			y = Math.max(y,-49)
			this.y = y
		},
		/**
		 * 拖动结束
		 */
		onTouchEnd(e){
			if(!this.touchFlag){
				this.touchFlag = true
				return
			}
			if(this.y === 0){
				this.refreshing = true
				this.$emit('update:refreshStatus',true)
				this.$emit('refresh')
			}else{
				this.y = -49
			}
		},

		onScrollToLower(){
			this.$emit('scrolltolower')
		}
	}
}
